<template>
<div class="addRemark">
    <!--添加编辑内容-->
    <slot name="remark"></slot>
    <div class="editRemark">
        <!--文本域-->
        <textarea :placeholder="placeholders" class="editMes" rows="2" v-model="limitInput"></textarea>
        <div class="addPic">
            <!--添加的图片-->
            <!--<div v-if="picArr" v-for="(item,index) in picArr" :src="item" @touchstart.prevent="previewTheImage(item)" :key="index" >
            	<img :src="item" @touchstart.prevent="previewTheImage(item)" :key="index" />
            	<span></span>
            </div>-->
            <div v-for="(item,index) in picArr" :key="index">
                <img :src="item" @click.prevent="previewTheImage(item)" />
                <span @click.prevent="deleThisPic(index)">
                    <i></i>
                </span>
            </div>
            <div @click.prevent="addImg" v-show="picArr.length<4">
                <img src="../../../assets/addimg.png" />
            </div>
        </div>
        <div class="addStatus">
            <!--计算图片个数和文字数量的-->
            <span>{{picArr?picArr.length:0}}/4</span>
            <span>{{limitInput.length}}/200</span>
        </div>
    </div>
    <transition name="addPic">
        <div class="addImgFloat" v-show="isAddImgShow">
            <!--添加图片的弹出层-->
            <transition name="addPicOption">
                <div v-show="isAddImgShow">
                    <span>拍一张照片</span>
                    <span>从相册中选择</span>
                    <span @click.prevent="isAddImgShow=!isAddImgShow">取消</span>
                </div>
            </transition>
        </div>
    </transition>
</div>
</template>
<script>
import {
    chooseImage,
    previewImage,
    getLocalImgData,
} from 'api/global'
export default {
    name: 'evaluateInput',
    props: ['picArr', 'placeholders'],
    data() {
        return {
            limitInput: '',
            isAddImgShow: false,
        }
    },
    watch: {
        limitInput(val) {
            if (val.length > 200) {
                this.limitInput = val.slice(0, 200);
            }
            this.$emit('limitinput', this.limitInput)
        },
    },
    methods: {
        deleThisPic(index) {
            this.picArr.splice(index, 1)
        },
        picToBase(localId) {
            return new Promise((resolve, reject) => {
                getLocalImgData(localId).then(imgRes => {
                    var re = /^data/g
                    if (!re.test(imgRes)) {
                        imgRes = 'data:image/jpg;base64,' + imgRes
                    }
                    //图片上传的数组，格式是base64
                    resolve(imgRes)
                }).catch(imgErr => {
                    console.error(imgErr)
                })
            })
        },
        addImg() {
            let chooseCount = 4 - this.picArr.length
            chooseImage(chooseCount).then(res => {
                //localIds的格式:["wxlocalResource://465437865486547686"] (苹果)
                //						:["weixin://resourceid/2c1e43254343"]{安卓}
                // Promise.all(res.map(localId => {
                //     return this.picToBase(localId)
                // })).then(imgBaseArr => {
                //     this.picArr = this.picArr.concat(imgBaseArr)
                // })

                // for (let localId of res) {
                //     this.picToBase(localId).then(imgRes => {
                //         this.picArr.push(imgRes)
                //     })
                // }
                let cont = 0
                let getImgBase64 = res => {
                    if (cont >= res.length) {
                        return
                    }
                    getLocalImgData(res[cont]).then(imgRes => {
                        var re = /^data/g
                        if (!re.test(imgRes)) {
                            imgRes = 'data:image/jpg;base64,' + imgRes
                        }
                        //图片上传的数组，格式是base64
                        this.picArr.push(imgRes)
                        cont++
                        getImgBase64(res)
                    }).catch(imgErr => {
                        console.error(imgErr)
                    })
                }
                getImgBase64(res)
            }).catch(err => {
                console.error(err)
            })
            //点击添加图片
            // this.isAddImgShow = true
        },
        previewTheImage(item) {
            previewImage(item, this.picArr)
        }
    }
}
</script>
<style lang="less">
@rem: 30rem;
@phoneH: 330/@rem;
.addRemark * {
    color: #333333;
}

.addRemark {
    margin-top: 20/@rem;
    h4 {
        height: 68/@rem;
        line-height: 68/@rem;
        font-size: 26/@rem;
        padding-left: 40/@rem;
        color: #929499;
    }
}

.editRemark {
    background: #fff;
    padding: 30/@rem 40/@rem 0;
    .editMes {
        width: 100%;
        border: none;
        text-align: left;
        line-height: 50/@rem;
        font-size: 30/@rem;
        color: #333333;
    }
    .addPic {
        display: flex;
        justify-content: flex-start;
        width: 100%;
        div {
            position: relative;
            width: 160/@rem;
            height: 160/@rem;
            margin-right: 10/@rem;
            img {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
            }
            span {
                position: absolute;
                display: flex;
                top: 0;
                right: 0;
                width: 44/@rem;
                height: 44/@rem;
                background-color: rgba(0, 0, 0, 0.1);
                border-radius: 0 0 0 10/@rem;
                i {
                    display: inline-block;
                    width: 21/@rem;
                    height: 21/@rem;
                    margin: auto;
                    background-image: url(../../../assets/XX.png);
                    background-clip: border-box;
                    background-size: cover;
                }
            }
        }
    }
    .addStatus {
        width: 100%;
        height: 50/@rem;
        color: #9597a0;
        line-height: 50/@rem;
        display: flex;
        justify-content: space-between;
        span {
            color: #cacaca;
            font-size: 24/@rem;
        }
    }
}

.addImgFloat {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    div {
        position: absolute;
        bottom: 0;
        left: 0;
        text-align: center;
        width: 100%;
        height: @phoneH;
        background-color: #fff;
        span {
            display: block;
            height: @phoneH/3;
            line-height: @phoneH/3;
            border-bottom: 1px solid #aaaaaa;
            color: #676767;
        }
        span:last-of-type {
            border-bottom: none;
        }
    }
}

.addPic-enter-active,
.addPic-leave-active {
    transition: all 0.25s ease-out;
}

.addPic-enter,
.addPic-leave-active {
    opacity: 0;
}

.addPicOption-enter-active,
.addPicOption-leave-active {
    transition: all 0.25s ease-out;
}

.addPicOption-enter,
.addPicOption-leave-active {
    transform: translateY(@phoneH);
}
</style>



